document.querySelector(function() {

    // Fixed header
    let header = document.querySelector("#header");
    let intro = document.querySelector("#intro");
    let introH = intro.innerHeight();
    let scrollPos = document.querySelector(window).scrollTop;
    let nav = document.querySelector("#nav");
    let navToggle = document.querySelector("#navToggle");

    checkScroll(scrollPos, introH)

    document.querySelector(window).addEventListener("scroll resize", function() {
        introH = intro.innerHeight();
        scrollPos = document.querySelector(this).scrollTop;

        checkScroll(scrollPos, introH);
    });

    function checkScroll(scrollPos, introH) {
        if( scrollPos > introH ) {
            header.classList.add("fixed");            
        } else {
            header.classList.remove("fixed");
        }
    }

    // Smooth scroll
    document.querySelector("[data-scroll]").addEventListener("click", function(event) {
        event.preventDefault();

        let elementId = document.querySelector(this).data('scroll');
        let elementOffset = document.querySelector(elementId).offset().top;

        nav.classList.remove("show");

        console.log(elementOffset);

        document.querySelector("html, body").animate({
            scrollTop: elementOffset - 70
        }, 700);
    });
})
